@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;600;700&display=swap');

/* -------- Layout & Typography -------- */

body {
  font-family: 'Exo 2', sans-serif;
  background-color: #2b3742;
  margin: 0;
  padding: 0;
  color: white;
}

.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

/* Container for logo and trademark */
.logo-container {
  display: inline-block;
  position: relative;
  width: fit-content;
  overflow: hidden;
}
.trademark {
  position: absolute;
  top: 2.4em; /* Adjust vertically */
  right: 5.6em; /* Adjust horizontally to hang off the "E" */
  font-size: 1.4rem;
  font-weight: bold;
  color: #8ef20b;
  pointer-events: none;
  z-index: 10;
  text-shadow: 0 0 4px #8ef20b;
}


.live-text {
  font-family: 'Exo 2', sans-serif;
  color: #ffffff; /* Neon cyan */
  /*background-color: rgba(0, 0, 0, 0.2); /* black with 60% opacity */
  backdrop-filter: blur(6px);
  padding: 6px 12px;
  border-radius: 4px;
  display: inline-block;
}

.live-text.surge {
  color: #ff00ff;
  border-color: #00ff88;
}

.live-text.warning {
  color: #ff3300;
  border-color: #ff3300;
}

.live-text.idle {
  color: #8ef20b;
  border-color: #00ffff;
}

.subscribe-btn {
  font-family: 'Exo 2', sans-serif;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  padding: 12px 24px;
  border: none;
  border-radius: 6px;
  color: #0a0a0a;
  background-color: #00ffff; /* Neon cyan */
  box-shadow: 0 0 8px #00ffff, 0 0 16px #00ffff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.subscribe-btn:hover {
  background-color: #00ff88; /* Neon green variant */
  box-shadow: 0 0 12px #00ff88, 0 0 24px #00ff88;
  color: #000;
}

.email-input {
  font-family: 'Exo 2', sans-serif;
  font-size: 1rem;
  padding: 12px 16px;
  margin-bottom:16px;
  width: 100%;
  max-width: 320px;
  border: 2px solid #00ffff; /* Neon cyan */
  border-radius: 6px;
  background-color: #0a0a0a;
  color: #ffffff;
  outline: none;
  box-shadow: 0 0 6px #00ffff;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.email-input::placeholder {
  color: #888;
  letter-spacing: 0.05em;
}

.email-input:focus {
  border-color: #00ff88; /* Neon green on focus */
  box-shadow: 0 0 10px #00ff88;
}

form#subscribe {
  background: #0f0f0f;
  padding: 2rem;
  border: 2px solid #8ef20b;
  font-family: 'Exo 2', sans-serif;
  color: #ffffff;
  max-width: 400px;
  margin: auto;
  box-shadow: 0 0 20px #8ef20b;
}

.glitch-label {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  display: block;
  text-shadow: 0 0 5px #ff00ff;
}

input[type="email"] {
  width: 100%;
  padding: 0.8rem;
  border: none;
  background: #1a1a1a;
  color: #00ffff;
}

.consent {
  margin-top: 1rem;
  font-size: 0.9rem;
}

.glitch-button {
  margin-top: 1.5rem;
  padding: 0.8rem 1.2rem;
  background: linear-gradient(90deg, #ff00ff, #00ffff);
  border: none;
  color: #000;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 0 10px #ff00ff;
}
.animate {
  font-family: 'Exo 2', sans-serif;
  font-size: 3rem;
  font-weight: 700;
  color: #8ef20b;
  opacity: 3;
  transform: scale(0.8);
  animation: popIn 1.2s ease-out forwards;
  position: relative;
}

/* Delay glitch until after popIn finishes */
.animate::before,
.animate::after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  color: #8ef20b;
  background: transparent;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  opacity: 0;
  animation-delay: 1.2s; /* match popIn duration */
}

.animate::before {
  color: #ff00c8;
  animation: glitchTop 1s infinite linear alternate-reverse;
}

.animate::after {
  color: #00fff7;
  animation: glitchBottom 1s infinite linear alternate-reverse;
}

@keyframes popIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  60% {
    opacity: 1;
    transform: scale(1.05);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes glitchTop {
  0% {
    clip: rect(2px, 9999px, 8px, 0);
    transform: translate(-2px, -2px);
    opacity: 1;
  }
  50% {
    clip: rect(4px, 9999px, 6px, 0);
    transform: translate(2px, -1px);
    opacity: 1;
  }
  100% {
    clip: rect(1px, 9999px, 5px, 0);
    transform: translate(-1px, 1px);
    opacity: 1;
  }
}

@keyframes glitchBottom {
  0% {
    clip: rect(12px, 9999px, 18px, 0);
    transform: translate(2px, 2px);
    opacity: 1;
  }
  50% {
    clip: rect(14px, 9999px, 16px, 0);
    transform: translate(-2px, 1px);
    opacity: 1;
  }
  100% {
    clip: rect(11px, 9999px, 15px, 0);
    transform: translate(1px, -1px);
    opacity: 1;
  }
}

.privacy-link {
  color: #0ff;
  text-decoration: none; /* optional: removes underline */
}

.privacy-link:hover {
  text-decoration: underline; /* optional: adds hover effect */
}

