.glitch-container {
  position: relative;
  display: inline-block;
  /* TODO play with these
  background-color: #000000;
  background: radial-gradient(circle, #ff00ff22 0%, #000000 80%);
  */
}

.glitch-base,
.glitch-layer {
  height: 40vmin;
  display: block;
  pointer-events: none;
  transition: all 0.3s ease-out;

  max-width: 90%;
}

.glitch-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: glitchPulse 0.25s infinite ease-in-out;
  mix-blend-mode: lighten;
  pointer-events: none;
  z-index: 4;
}

.glitch-layer.red {
  filter: hue-rotate(0deg) blur(1px);
}

.glitch-layer.blue {
  filter: hue-rotate(180deg) blur(1px);
}

.glitch-container.clean .glitch-base {
  filter: drop-shadow(0 0 2px #00ffff); /* Was 4px */

}

.glitch-base {
  filter: contrast(110%) brightness(105%);
}

/* Animate chaotic movement */
@keyframes surge-offset {
  0%   { transform: translate(0, 0); }
  25%  { transform: translate(-2px, 2px); }
  50%  { transform: translate(2px, -2px); }
  75%  { transform: translate(-1px, 1px); }
  100% { transform: translate(0, 0); }
}

@keyframes glitchErratic {
  0% {
    transform: skew(0.5deg, -0.5deg) translate(1px, 1px);
    opacity: 0.95;
  }
  20% {
    transform: skew(-1deg, 1deg) translate(-1px, 2px);
    opacity: 0.75;
  }
  40% {
    transform: skew(2deg, 0deg) translate(2px, -2px);
    opacity: 0.85;
  }
  60% {
    transform: skew(-2deg, 2deg) translate(-2px, 1px);
    opacity: 0.9;
  }
  80% {
    transform: skew(1deg, -1deg) translate(0px, -3px);
    opacity: 0.8;
  }
  100% {
    transform: skew(0deg, 0deg) translate(0px, 0px);
    opacity: 1;
  }
}

@keyframes glitchPulse {
  0%   { transform: translate(0.4px, 0.4px) skew(0.2deg, -0.2deg); opacity: 0.94; }
  20%  { transform: translate(-0.6px, 0.6px) skew(-0.3deg, 0.3deg); opacity: 0.86; }
  40%  { transform: translate(0.5px, -0.5px) skew(0.1deg, -0.1deg); opacity: 0.89; }
  60%  { transform: translate(-0.4px, 0.4px) skew(-0.2deg, 0.2deg); opacity: 0.91; }
  80%  { transform: translate(0.3px, -0.3px) skew(0.1deg, -0.1deg); opacity: 0.88; }
  100% { transform: none; opacity: 0.75; filter: none; }
}


.glitch-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  /*
  background: repeating-linear-gradient(
    0deg,
    rgba(255, 255, 255, 0.02),
    rgba(255, 255, 255, 0.06) 2px,
    transparent 2px,
    transparent 4px
  );*/
  animation: scanlines 1.5s linear infinite;
  z-index: 5;
}

@keyframes scanlines {
  0% { background-position: 0 0; }
  100% { background-position: 0 4px; }
}

.code-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  line-height: 1.4;
  color: #00ffff; /* Solid neon cyan */
  text-shadow:
    0 0 0.5px #00ffff,       /* Tight glow */
    0 0 1px rgba(0, 255, 255, 0.2); /* Gentle highlight */
  white-space: pre-wrap;
  padding: 20px;
  animation: code-scroll 1s linear infinite;
  z-index: 6;
  pointer-events: none;
}

@keyframes code-scroll {
  0% { transform: translateY(0); }
  100% { transform: translateY(-10px); }
}

.glitch-container.cooldown .glitch-layer {
  animation: none;
  opacity: 0.3;
  filter: none;
}

.glitch-container.cooldown .glitch-base {
  filter: contrast(120%) brightness(110%) drop-shadow(0 0 1px #00ffff);
}

.glitch-container.surge-magenta {
  background: radial-gradient(circle, #ff00ff44 0%, #000000 80%);
  animation: backgroundPulse 1s ease-out;
}

@keyframes backgroundPulse {
  0% {
    background: radial-gradient(circle, #ff00ffaa 0%, #000000 80%);
  }
  50% {
    background: radial-gradient(circle, #ff00ff44 0%, #000000 80%);
  }
  100% {
    background: radial-gradient(circle, #ff00ff22 0%, #000000 80%);
  }
}

