/* Footer.css */
.app-footer {
  text-align: center;
  padding: 20px;
  background-color: #111;
  color: #eee;
  font-size: 0.9rem;
  position: relative;
  bottom: 0;
  width: 100%;
  overflow-x:hidden;
}

.footer-link {
  color: #0ff;
  text-decoration: none;
}

.footer-link:hover {
  text-decoration: underline;
}
