body {
  background-color: #0d0d0d;
  color: #e0e0e0;
  font-family: 'Courier New', Courier, monospace;
  line-height: 1.6;
  padding: 2rem;
}

.container {
  max-width: 800px;
  margin: auto;
  padding: 2rem;
  background: rgba(20, 20, 20, 0.9);
  border: 1px solid #8ef20b;
  box-shadow: 0 0 20px #00ffff55;
}

h1.glitch {
  font-size: 3rem;
  color: #8ef20b;
  position: relative;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

h1.glitch::before,
h1.glitch::after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  width: 100%;
  overflow: hidden;
  color: #ff00ff;
  clip: rect(0, 900px, 0, 0);
  animation: glitch 2s infinite linear alternate-reverse;
}

h1.glitch::after {
  color: #ff0000;
  animation-delay: 0.5s;
}

@keyframes glitch {
  0% {
    clip: rect(42px, 9999px, 44px, 0);
    transform: skew(0.5deg);
  }
  5% {
    clip: rect(12px, 9999px, 18px, 0);
    transform: skew(0.5deg);
  }
  10% {
    clip: rect(85px, 9999px, 90px, 0);
    transform: skew(0.5deg);
  }
  15% {
    clip: rect(45px, 9999px, 50px, 0);
    transform: skew(0.5deg);
  }
  20% {
    clip: rect(25px, 9999px, 30px, 0);
    transform: skew(0.5deg);
  }
  100% {
    clip: rect(0, 9999px, 0, 0);
    transform: skew(0.5deg);
  }
}

h2 {
  color: #8ef20b;
  margin-top: 2rem;
  font-size: 1.5rem;
  border-bottom: 1px solid #00ffff55;
  padding-bottom: 0.5rem;
}

p {
  margin-top: 0.5rem;
}

.fade-in {
  animation: fadeInGlitch 1.2s ease-out;
}

@keyframes fadeInGlitch {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.98);
    filter: blur(4px);
  }
  50% {
    opacity: 0.5;
    transform: translateY(10px) scale(1.01);
    filter: blur(2px);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0);
  }
}
